'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { apiHelpers } from '@/lib/apiClient'

interface User {
  id: string
  name: string
  title: string
  company: string
  location: string
  bio: string
  skills: string[]
  level: number
  xpPoints: number
  isConnected: boolean
  mutualConnections: number
}

interface ConnectionRequest {
  id: string
  fromUser: User
  message: string
  timestamp: string
}

export default function ConnectionsPage() {
  const [users, setUsers] = useState<User[]>([])
  const [connections, setConnections] = useState<User[]>([])
  const [requests, setRequests] = useState<ConnectionRequest[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  // Mock data for demonstration
  const mockUsers: User[] = [
    {
      id: '1',
      name: 'Sarah Johnson',
      title: 'Senior Data Scientist',
      company: 'TechCorp',
      location: 'San Francisco, CA',
      bio: 'Passionate about machine learning and AI applications in healthcare. Love mentoring junior developers.',
      skills: ['Python', 'Machine Learning', 'TensorFlow', 'Data Visualization'],
      level: 12,
      xpPoints: 4500,
      isConnected: false,
      mutualConnections: 3
    },
    {
      id: '2',
      name: 'Mike Chen',
      title: 'Product Manager',
      company: 'InnovateLab',
      location: 'New York, NY',
      bio: 'Building products that make a difference. Former engineer turned PM with a focus on user experience.',
      skills: ['Product Strategy', 'User Research', 'Agile', 'Analytics'],
      level: 9,
      xpPoints: 3200,
      isConnected: false,
      mutualConnections: 1
    },
    {
      id: '3',
      name: 'Emily Rodriguez',
      title: 'UX Designer',
      company: 'DesignStudio',
      location: 'Austin, TX',
      bio: 'Creating intuitive and beautiful user experiences. Advocate for accessibility and inclusive design.',
      skills: ['UI/UX Design', 'Figma', 'User Research', 'Prototyping'],
      level: 8,
      xpPoints: 2800,
      isConnected: false,
      mutualConnections: 2
    },
    {
      id: '4',
      name: 'David Kim',
      title: 'Software Engineer',
      company: 'CodeWorks',
      location: 'Seattle, WA',
      bio: 'Full-stack developer with expertise in React and Node.js. Open source contributor and tech blogger.',
      skills: ['React', 'Node.js', 'TypeScript', 'AWS'],
      level: 10,
      xpPoints: 3800,
      isConnected: false,
      mutualConnections: 0
    },
    {
      id: '5',
      name: 'Lisa Wang',
      title: 'Marketing Director',
      company: 'GrowthCo',
      location: 'Los Angeles, CA',
      bio: 'Digital marketing strategist with a data-driven approach. Helping startups scale their user acquisition.',
      skills: ['Digital Marketing', 'Growth Hacking', 'Analytics', 'Content Strategy'],
      level: 11,
      xpPoints: 4100,
      isConnected: false,
      mutualConnections: 1
    }
  ]

  const mockConnections: User[] = [
    {
      id: '6',
      name: 'John Smith',
      title: 'Senior Developer',
      company: 'WebTech',
      location: 'Boston, MA',
      bio: 'Experienced full-stack developer specializing in modern web technologies.',
      skills: ['JavaScript', 'React', 'Python', 'Docker'],
      level: 13,
      xpPoints: 5200,
      isConnected: true,
      mutualConnections: 5
    },
    {
      id: '7',
      name: 'Anna Martinez',
      title: 'Data Analyst',
      company: 'DataInsights',
      location: 'Chicago, IL',
      bio: 'Turning data into actionable insights. Passionate about statistical analysis and visualization.',
      skills: ['SQL', 'Python', 'Tableau', 'Statistics'],
      level: 7,
      xpPoints: 2300,
      isConnected: true,
      mutualConnections: 2
    }
  ]

  const mockRequests: ConnectionRequest[] = [
    {
      id: '1',
      fromUser: {
        id: '8',
        name: 'Robert Taylor',
        title: 'DevOps Engineer',
        company: 'CloudSys',
        location: 'Denver, CO',
        bio: 'Infrastructure automation enthusiast. Building scalable cloud solutions.',
        skills: ['AWS', 'Kubernetes', 'Terraform', 'CI/CD'],
        level: 9,
        xpPoints: 3400,
        isConnected: false,
        mutualConnections: 1
      },
      message: 'Hi! I saw your profile and would love to connect. We have similar interests in cloud technologies.',
      timestamp: '2024-01-20T10:30:00Z'
    }
  ]

  useEffect(() => {
    const fetchConnectionsData = async () => {
      try {
        setLoading(true)
        setError(null)
        
        // Try to fetch from API, fallback to mock data
        try {
          const [usersData, connectionsData, requestsData] = await Promise.all([
            apiHelpers.get<User[]>('/users'),
            apiHelpers.get<User[]>('/connections'),
            apiHelpers.get<ConnectionRequest[]>('/connections/requests')
          ])
          
          setUsers(usersData)
          setConnections(connectionsData)
          setRequests(requestsData)
        } catch (apiError) {
          // Use mock data if API is not available
          console.log('API not available, using mock data')
          setUsers(mockUsers)
          setConnections(mockConnections)
          setRequests(mockRequests)
        }
      } catch (err) {
        setError('Failed to load connections data')
        // Fallback to mock data
        setUsers(mockUsers)
        setConnections(mockConnections)
        setRequests(mockRequests)
      } finally {
        setLoading(false)
      }
    }

    fetchConnectionsData()
  }, [])

  const handleConnect = async (userId: string) => {
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 500))
      
      // Update local state
      setUsers(users.map(user => 
        user.id === userId 
          ? { ...user, isConnected: true }
          : user
      ))
    } catch (err) {
      console.error('Failed to connect:', err)
    }
  }

  const handleAcceptRequest = async (requestId: string) => {
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 500))
      
      // Move user from requests to connections
      const request = requests.find(r => r.id === requestId)
      if (request) {
        setConnections([...connections, { ...request.fromUser, isConnected: true }])
        setRequests(requests.filter(r => r.id !== requestId))
      }
    } catch (err) {
      console.error('Failed to accept request:', err)
    }
  }

  const handleDeclineRequest = async (requestId: string) => {
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 500))
      
      setRequests(requests.filter(r => r.id !== requestId))
    } catch (err) {
      console.error('Failed to decline request:', err)
    }
  }

  const filteredUsers = users.filter(user =>
    user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.company.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.skills.some(skill => skill.toLowerCase().includes(searchTerm.toLowerCase()))
  )

  const filteredConnections = connections.filter(user =>
    user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.company.toLowerCase().includes(searchTerm.toLowerCase())
  )

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Professional Network</h1>
        <p className="text-muted-foreground">
          Connect with professionals, expand your network, and grow your career
        </p>
      </div>

      {error && (
        <Card className="mb-6 border-destructive">
          <CardContent className="pt-6">
            <p className="text-destructive">{error}</p>
          </CardContent>
        </Card>
      )}

      {/* Search */}
      <div className="mb-6">
        <Input
          placeholder="Search professionals by name, title, company, or skills..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="max-w-md"
        />
      </div>

      <Tabs defaultValue="discover" className="space-y-6">
        <TabsList>
          <TabsTrigger value="discover">Discover People</TabsTrigger>
          <TabsTrigger value="connections">
            My Connections ({connections.length})
          </TabsTrigger>
          <TabsTrigger value="requests">
            Requests ({requests.length})
          </TabsTrigger>
        </TabsList>

        <TabsContent value="discover" className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredUsers.map((user) => (
              <Card key={user.id} className="hover:shadow-md transition-shadow">
                <CardHeader>
                  <div className="flex items-center space-x-3">
                    <div className="w-12 h-12 bg-primary rounded-full flex items-center justify-center">
                      <span className="text-lg font-bold text-primary-foreground">
                        {user.name.split(' ').map(n => n[0]).join('')}
                      </span>
                    </div>
                    <div className="flex-1 min-w-0">
                      <CardTitle className="text-lg truncate">{user.name}</CardTitle>
                      <p className="text-sm text-muted-foreground truncate">
                        {user.title} at {user.company}
                      </p>
                    </div>
                  </div>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div>
                    <p className="text-sm text-muted-foreground mb-1">{user.location}</p>
                    <p className="text-sm text-foreground line-clamp-2">{user.bio}</p>
                  </div>
                  
                  <div>
                    <p className="text-sm font-medium text-foreground mb-2">Skills</p>
                    <div className="flex flex-wrap gap-1">
                      {user.skills.slice(0, 3).map((skill) => (
                        <Badge key={skill} variant="secondary" className="text-xs">
                          {skill}
                        </Badge>
                      ))}
                      {user.skills.length > 3 && (
                        <Badge variant="outline" className="text-xs">
                          +{user.skills.length - 3} more
                        </Badge>
                      )}
                    </div>
                  </div>

                  <div className="flex items-center justify-between text-sm text-muted-foreground">
                    <span>Level {user.level}</span>
                    <span>{user.xpPoints.toLocaleString()} XP</span>
                  </div>

                  {user.mutualConnections > 0 && (
                    <p className="text-xs text-muted-foreground">
                      {user.mutualConnections} mutual connection{user.mutualConnections !== 1 ? 's' : ''}
                    </p>
                  )}

                  <div className="flex space-x-2">
                    <Button 
                      onClick={() => handleConnect(user.id)}
                      disabled={user.isConnected}
                      className="flex-1"
                      size="sm"
                    >
                      {user.isConnected ? 'Connected' : 'Connect'}
                    </Button>
                    <Button variant="outline" size="sm">
                      Message
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="connections" className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredConnections.map((connection) => (
              <Card key={connection.id} className="hover:shadow-md transition-shadow">
                <CardHeader>
                  <div className="flex items-center space-x-3">
                    <div className="w-12 h-12 bg-primary rounded-full flex items-center justify-center">
                      <span className="text-lg font-bold text-primary-foreground">
                        {connection.name.split(' ').map(n => n[0]).join('')}
                      </span>
                    </div>
                    <div className="flex-1 min-w-0">
                      <CardTitle className="text-lg truncate">{connection.name}</CardTitle>
                      <p className="text-sm text-muted-foreground truncate">
                        {connection.title} at {connection.company}
                      </p>
                    </div>
                  </div>
                </CardHeader>
                <CardContent className="space-y-4">
                  <p className="text-sm text-muted-foreground">{connection.location}</p>
                  
                  <div className="flex items-center justify-between text-sm text-muted-foreground">
                    <span>Level {connection.level}</span>
                    <span>{connection.xpPoints.toLocaleString()} XP</span>
                  </div>

                  <div className="flex space-x-2">
                    <Button variant="outline" size="sm" className="flex-1">
                      Message
                    </Button>
                    <Button variant="outline" size="sm">
                      View Profile
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="requests" className="space-y-6">
          {requests.length === 0 ? (
            <Card>
              <CardContent className="pt-6 text-center">
                <p className="text-muted-foreground">No pending connection requests</p>
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-4">
              {requests.map((request) => (
                <Card key={request.id}>
                  <CardContent className="pt-6">
                    <div className="flex items-start space-x-4">
                      <div className="w-12 h-12 bg-primary rounded-full flex items-center justify-center">
                        <span className="text-lg font-bold text-primary-foreground">
                          {request.fromUser.name.split(' ').map(n => n[0]).join('')}
                        </span>
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center justify-between mb-2">
                          <div>
                            <h3 className="font-medium text-foreground">{request.fromUser.name}</h3>
                            <p className="text-sm text-muted-foreground">
                              {request.fromUser.title} at {request.fromUser.company}
                            </p>
                          </div>
                          <span className="text-xs text-muted-foreground">
                            {new Date(request.timestamp).toLocaleDateString()}
                          </span>
                        </div>
                        
                        <p className="text-sm text-foreground mb-4">{request.message}</p>
                        
                        <div className="flex space-x-2">
                          <Button 
                            onClick={() => handleAcceptRequest(request.id)}
                            size="sm"
                          >
                            Accept
                          </Button>
                          <Button 
                            onClick={() => handleDeclineRequest(request.id)}
                            variant="outline" 
                            size="sm"
                          >
                            Decline
                          </Button>
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </TabsContent>
      </Tabs>
    </div>
  )
}
